import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { isNonEmptyArray } from '@webui-one/core/utils/array';
import {
    selectedDeviceRowsSelector,
    selectDeviceGroupsForDevices,
} from 'modules/devices/selectors';
import {
    addDevicesToGroup,
    loadDeviceGroups,
    loadDeviceGroupsForDevices,
    removeDevicesFromGroup,
} from 'modules/devices/actions';
import LabelsMenu from './LabelsMenu';

class LabelsMenuContainer extends React.Component {
    constructor(props) {
        super(props);
        this.onShowMenu = this.onShowMenu.bind(this);
        this.onHideMenu = this.onHideMenu.bind(this);
        this.onAddDevices = this.onAddDevices.bind(this);
        this.onRemoveDevices = this.onRemoveDevices.bind(this);
        this.state = {
            menuOpen: false,
            addedOrRemoved: false,
        };
    }

    onAddDevices(devices, group) {
        const { onAddDevicesToGroup } = this.props;
        onAddDevicesToGroup(devices, group);
        this.setState({ addedOrRemoved: true });
    }

    onRemoveDevices(devices, group) {
        const { onRemoveDevicesFromGroup } = this.props;
        onRemoveDevicesFromGroup(devices, group);
        this.setState({ addedOrRemoved: true });
    }

    onHideMenu() {
        const { onLoadDeviceGroups } = this.props;
        const { addedOrRemoved } = this.state;
        if (addedOrRemoved) {
            onLoadDeviceGroups();
        }
        this.setState({ menuOpen: false, addedOrRemoved: false });
    }

    onShowMenu() {
        const {
            devices,
            onGetDeviceGroupsForSelectedDevices,
        } = this.props;
        if (isNonEmptyArray(devices)) {
            this.setState({ menuOpen: true });
            onGetDeviceGroupsForSelectedDevices();
        }
    }

    render() {
        const {
            devices,
            deviceGroups,
        } = this.props;
        const { menuOpen } = this.state;

        return (
            <LabelsMenu
                deviceGroups={deviceGroups}
                devices={devices.map(d => d.data.name)}
                menuOpen={menuOpen}
                onAddDevicesToGroup={this.onAddDevices}
                onRemoveDevicesFromGroup={this.onRemoveDevices}
                onShowMenu={this.onShowMenu}
                onHideMenu={this.onHideMenu}
                enabled={isNonEmptyArray(devices)}
            />
        );
    }
}

LabelsMenuContainer.propTypes = {
    devices: PropTypes.arrayOf(Object),
    deviceGroups: PropTypes.arrayOf(PropTypes.string),
    onAddDevicesToGroup: PropTypes.func,
    onGetDeviceGroupsForSelectedDevices: PropTypes.func,
    onLoadDeviceGroups: PropTypes.func,
    onRemoveDevicesFromGroup: PropTypes.func,
};

LabelsMenuContainer.defaultProps = {
    deviceGroups: [],
    devices: [],
    onAddDevicesToGroup: () => undefined,
    onGetDeviceGroupsForSelectedDevices: () => (null),
    onLoadDeviceGroups: () => undefined,
    onRemoveDevicesFromGroup: () => undefined,
};

const mapDispatchToProps = dispatch => ({
    onGetDeviceGroupsForSelectedDevices() {
        dispatch(loadDeviceGroupsForDevices());
    },
    onLoadDeviceGroups() {
        dispatch(loadDeviceGroups());
    },
    onRemoveDevicesFromGroup(devices, group) {
        dispatch(removeDevicesFromGroup(devices, group));
    },
    onAddDevicesToGroup(devices, group) {
        dispatch(addDevicesToGroup(devices, group));
    },
});

const mapStateToProps = state =>
    ({
        devices: selectedDeviceRowsSelector(state),
        deviceGroups: selectDeviceGroupsForDevices(state),
    });

export default connect(
    mapStateToProps,
    mapDispatchToProps,
)(LabelsMenuContainer);

export {
    LabelsMenuContainer as _LabelsMenuContainer,
    mapDispatchToProps as _mapDispatchToProps,
    mapStateToProps as _mapStateToProps,
};
