import React, { useEffect } from 'react';
import PropTypes from 'prop-types';
import { Link } from '@webui-one/router';
import * as defaultStyles from '@webui-one/styles/ApplicationDefaultStyles';
import Loader from '@webui-one/components/Loading/Loader';
import styles from './ManagerItemsStyles';


const ItemLinks = ({ items }) => items.map(item => (
    <Link
        title={item.name}
        key={item.path}
        style={styles.itemsLink}
        to={`/webui-one/ConfigurationEditor${item.path}`}
    >
        {item.name}
    </Link>
));

const ItemGroupList = ({ itemGroups }) => itemGroups.map(itemGroup => (
    <div data-handle="manager-items-group" style={styles.group} key={itemGroup.path}>
        <h2 style={styles.groupTitle}>{itemGroup.name}</h2>
        <ItemLinks items={itemGroup.children} />
    </div>
));

export default function ManagerItems({ path, onQueryData, rows, isLoading }) {
    useEffect(() => {
        onQueryData(path);
    }, [onQueryData, path]);

    return (
        <div style={styles.container}>
            {isLoading && (
                <div style={styles.loaderContainer}>
                    <Loader color={defaultStyles.color2} />
                </div>
            )}
            { rows.length === 0 && !isLoading &&
                <p>No items</p>
            }

            <ItemGroupList itemGroups={rows} />
        </div>
    );
}

ManagerItems.propTypes = {
    path: PropTypes.string.isRequired,
    onQueryData: PropTypes.func,
    isLoading: PropTypes.bool,
    rows: PropTypes.arrayOf(Object),
};

ManagerItems.defaultProps = {
    isLoading: false,
    rows: [],
};
