export const getDevices = ({
    chunkSize,
    offset,
    columnKey,
    sortorder,
    xpathExpression,
    selection = QUERY_SELECTION,
    funcs: {
        getSchema = _getSchema,
        query = _query,
    } = {},
} = {}) => async (dispatch) => {
    await dispatch(gettingDevicesStart());
    await dispatch(getSchema({
        path: devicesRootPath,
        params: {
            levels: -1,
            stop_on_list: true,
        },
        reload: true,
    }));

    const querySelection = uniq([
        'name',
        ...selection,
    ]);
    const queryResult = await dispatch(query({
        selection: querySelection,
        path: deviceListPath,
        sort: columnKey,
        sortOrder: sortorder,
        initialOffset: offset,
        chunkSize,
        result_as: 'string',
        xpathExpr: xpathExpression,
        contextNode: devicesRootPath,
    }));

    await dispatch(gettingDevicesComplete());
    return queryResult;
};

export const loadDeviceGroups = ({
    funcs: {
        query = jsonrpcQuery,
        readThunk = read,
    } = {},
} = {}) => (
    async (dispatch) => {
        dispatch(loadDeviceGroupsStartAction());
        try {
            const { th } = await dispatch(readThunk());
            const xpathExpr = 'device-group[count(device-name) > 0]';
            const result = await query({
                th,
                selection: ['name'],
                contextNode: '/ncs:devices',
                xpathExpr,
            });
            const groups = flatten(result.results);
            return dispatch(loadDeviceGroupsCompleteAction(groups));
        } catch (error) {
            return dispatch(loadDeviceGroupsErrorAction(error));
        }
    }
);

const groupsForDevicesRequestMapper = th => xpathExpr => ({
    method: 'query',
    params: {
        th,
        selection: ['name'],
        context_node: '/ncs:devices',
        xpath_expr: xpathExpr,
    },
});
export const loadDeviceGroupsForDevices = ({
    funcs: {
        jsonrpcBatch = _jsonrpcBatch,
        readThunk = read,
        selectedDeviceRows = selectedDeviceRowsSelector,
    } = {},
} = {}) => (
    async (dispatch, getState) => {
        dispatch(loadDeviceGroupsForDevicesStartAction());
        try {
            const selectedDevices = selectedDeviceRows(getState())
                .reduce((deviceNames, device) => {
                    if (device.data.isSelected) {
                        deviceNames.push(device.data.name);
                    }
                    return deviceNames;
                }, []);
            if (!isNonEmptyArray(selectedDevices)) {
                return dispatch(loadDeviceGroupsForDevicesCompleteAction([]));
            }
            const { th } = await dispatch(readThunk());
            const xpathExprList = selectedDevices.map(deviceName => (
                `device-group[device-name="${deviceName}"]`
            ));
            const requests = xpathExprList.map(groupsForDevicesRequestMapper(th));
            const responses = await jsonrpcBatch({ requests });
            const groups = uniq(flatten(responses.map(r => r.result.results)));
            return dispatch(loadDeviceGroupsForDevicesCompleteAction(groups));
        } catch (error) {
            return dispatch(loadDeviceGroupsForDevicesErrorAction(error));
        }
    }
);

export const getValueFromResult = (result) => {
    if (!result || !result.results) return [];
    return result.results.reduce((acc, obj) => [...acc, ...Object.values(obj)], []);
};

const addValuesRequestMapper = (group, th) => deviceName => ({
    method: 'create',
    params: {
        path: getDeviceGroupDeviceListDevicePath(group, deviceName),
        th,
    },
});

const addValues = async (group, valuesToAdd, th, {
    funcs: {
        jsonrpcBatch = _jsonrpcBatch,
        query = jsonrpcQuery,
    } = {},
} = {}) => {
    const path = getDeviceGroupDeviceListPath(group);
    const result = await query({ selection: undefined, path, th });
    const value = getValueFromResult(result);
    const minValuesToAdd = difference(valuesToAdd, value);
    if (isEmpty(minValuesToAdd)) return value;
    const requests = minValuesToAdd.map(addValuesRequestMapper(group, th));
    return jsonrpcBatch({ requests });
};

export const addDevicesToGroup = (devices, group, {
    funcs: {
        addValuesFunc = addValues,
        createAllowExists = _createAllowExists,
        loadDeviceGroupsFunc = loadDeviceGroupsForDevices,
        writeThunk = write,
    } = {},
} = {}) => (
    async (dispatch) => {
        dispatch(addDevicesToGroupStartAction(group));
        const groupPath = getDeviceGroupDeviceListPath(group);

        try {
            const { th } = await dispatch(writeThunk());
            await createAllowExists({ path: groupPath, th });
            const createResult = await addValuesFunc(group, devices, th);
            if (isNonEmptyArray(createResult) && createResult[0].error) {
                throw createResult[0].error;
            }
            await dispatch(loadDeviceGroupsFunc());
            return dispatch(addDevicesToGroupCompleteAction(group));
        } catch (error) {
            return dispatch(addDevicesToGroupErrorAction(group, error));
        }
    }
);

const deleteRequestMapper = (path, th) => deviceName => ({
    method: 'delete',
    params: {
        th,
        path: `${path}{${deviceName}}`,
    },
});
export const removeDevicesFromGroup = (devices, group, {
    funcs: {
        loadDeviceGroupsFunc = loadDeviceGroupsForDevices,
        query = jsonrpcQuery,
        jsonrpcBatch = _jsonrpcBatch,
        writeThunk = write,
    } = {},
} = {}) => (
    async (dispatch) => {
        dispatch(removeDevicesFromGroupStartAction(group));
        try {
            const { th } = await dispatch(writeThunk());
            const path = getDeviceGroupDeviceListPath(group);
            const result = await query({
                path,
                th,
            });
            const queryValue = getValueFromResult(result);
            const valuesToRm = intersect(queryValue, devices);
            if (!isNonEmptyArray(valuesToRm)) {
                // Skip deleting if the devices do not belong
                // to the device-group
                return dispatch(
                    removeDevicesFromGroupCompleteAction(group, []),
                );
            }
            const requests = valuesToRm.map(deleteRequestMapper(path, th));
            const deleteResult = await jsonrpcBatch({ requests });
            if (isNonEmptyArray(deleteResult) && deleteResult[0].error) {
                throw deleteResult[0].error;
            }
            await dispatch(loadDeviceGroupsFunc());
            return dispatch(removeDevicesFromGroupCompleteAction(group, valuesToRm));
        } catch (error) {
            return dispatch(removeDevicesFromGroupErrorAction(group, error));
        }
    }
);

const deviceRE = /^(\/ncs:devices\/device\{([^}]+)\}).*/;

export const loadModules = (configPath, {
    funcs: {
        readThunk = read,
        query = jsonrpcQuery,
    } = {},
} = {}) => (
    (dispatch) => {
        const match = deviceRE.exec(configPath);
        if (!match) return Promise.resolve();
        const path = `${match[1]}/module`;
        dispatch(loadModulesStartAction(path));

        return dispatch(readThunk()).then(({ th }) => (
            query({ th, selection: ['name'], path })
        )).then(result => (
            dispatch(loadModulesCompleteAction(
                path,
                // NOTE(dbildstr): return array of strings instead of array of array of strings
                result.results.map(module => module[0]),
            ))
        )).catch(error => (
            dispatch(loadModulesErrorAction(path, error))
        ));
    }
);

export const addDevice = ({
    createPath,
    chunkSize,
    offset,
    columnKey,
    sortorder,
    xpathExpression,
    funcs: {
        createDisallowExistsThunk = _createDisallowExistsThunk,
        getDevicesThunk = getDevices,
    } = {},
}) => (
    dispatch => dispatch(createDisallowExistsThunk({ path: createPath })).then((result) => {
        if (result.error) return result;
        return dispatch(
            getDevicesThunk({
                chunkSize,
                offset,
                columnKey,
                sortorder,
                xpathExpression,
            }),
        );
    })
);

