import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import ToolTip from '@webui-one/components/ToolTip/ToolTip';
import AddSymbol from '@webui-one/styles/static/AddSymbol';
import style from './AddListItemButtonStyles';
import ModalSchemaInputContainer from 'Common/Components/Modal/ModalSchemaInputContainer';
import {
    selectDeviceOffset,
    selectDeviceSortColumn,
    selectDeviceSortOrder,
    selectDeviceXpathExpression,
} from 'modules/devices/selectors';
import { addDevice } from 'modules/devices/actions';
import { deviceListPath, DEVICE_CHUNK_SIZE } from 'modules/devices/paths';

const ModalSchemaPaths = [deviceListPath];

class AddDevicesContainer extends React.Component {
    constructor(props) {
        super(props);
        this.showTooltip = this.showTooltip.bind(this);
        this.hideTooltip = this.hideTooltip.bind(this);
        this.showAddDeviceModal = this.showAddDeviceModal.bind(this);
        this.onConfirm = this.onConfirm.bind(this);
        this.onClose = this.onClose.bind(this);
        this.setTooltipRef = this.setTooltipRef.bind(this);
        this.state = {
            showAddDeviceModal: false,
            errorOnCreate: undefined,
            isLoading: false,
        };
    }

    onConfirm(createPath) {
        const {
            offset,
            onAddDevice,
            sortColumn,
            sortOrder,
            xpathExpression,
        } = this.props;
        this.setState({ isLoading: true });
        return onAddDevice(
            createPath,
            offset,
            sortColumn,
            sortOrder,
            xpathExpression,
        ).then((result) => {
            this.setState({ isLoading: false });
            if (result.error) {
                this.setState({ errorOnCreate: result.error });
            } else {
                this.onClose();
            }
        });
    }

    onClose() {
        this.setState({ showAddDeviceModal: false, errorOnCreate: undefined });
    }

    setTooltipRef(tt) {
        this.tooltip = tt;
    }

    showAddDeviceModal() {
        this.setState({ showAddDeviceModal: true });
    }

    showTooltip() {
        this.tooltip.show();
    }

    hideTooltip() {
        this.tooltip.hide();
    }

    render() {
        const { showAddDeviceModal, isLoading, errorOnCreate } = this.state;
        return (
            <div>
                <ToolTip
                    direction="right"
                    toolTipText="add device"
                    ref={this.setTooltipRef}
                />
                <button
                    type="button"
                    data-handle="add-list-item-button"
                    style={style.enabled}
                    onClick={this.showAddDeviceModal}
                    onMouseDown={this.showTooltip}
                    onMouseUp={this.hideTooltip}
                    onMouseEnter={this.showTooltip}
                    onMouseLeave={this.hideTooltip}
                >
                    <AddSymbol />
                </button>
                {showAddDeviceModal && (
                    <ModalSchemaInputContainer
                        title="Add device"
                        isLoading={isLoading}
                        paths={ModalSchemaPaths}
                        onConfirm={this.onConfirm}
                        onClose={this.onClose}
                        closeOnConfirm={false}
                        error={errorOnCreate}
                    />
                )}
            </div>
        );
    }
}

AddDevicesContainer.propTypes = {
    offset: PropTypes.number,
    onAddDevice: PropTypes.func,
    sortColumn: PropTypes.oneOfType([
        PropTypes.string,
        PropTypes.arrayOf(PropTypes.string),
    ]),
    sortOrder: PropTypes.string,
    xpathExpression: PropTypes.string,
};

const mapStateToProps = state => ({
    offset: selectDeviceOffset(state),
    sortColumn: selectDeviceSortColumn(state),
    sortOrder: selectDeviceSortOrder(state),
    xpathExpression: selectDeviceXpathExpression(state),
});

const mapDispatchToProps = dispatch => ({
    onAddDevice(
        createPath,
        offset,
        columnKey,
        sortorder,
        xpathExpression,
    ) {
        return dispatch(
            addDevice({
                createPath,
                chunkSize: DEVICE_CHUNK_SIZE,
                offset,
                columnKey,
                sortorder,
                xpathExpression,
            }),
        );
    },
});

export default connect(
    mapStateToProps,
    mapDispatchToProps,
)(AddDevicesContainer);

export {
    AddDevicesContainer as _AddDevicesContainer,
};
