import React, { useRef } from 'react';
import PropTypes from 'prop-types';
import RemoveSymbol from '@webui-one/styles/static/RemoveSymbol';
import * as defaultStyles from '@webui-one/styles/ApplicationDefaultStyles';
import ToolTip from '@webui-one/components/ToolTip/ToolTip';
import style from './AddListItemButtonStyles';

function RemoveDevicesButton(props) {
    const toolTipRef = useRef();

    function showTooltip() {
        toolTipRef.current.show();
    }

    function hideTooltip() {
        toolTipRef.current.hide();
    }

    const { disabled, onRemoveDevices } = props;

    const onClick = !disabled ? onRemoveDevices : () => null;

    return (
        <div>
            <button
                type="button"
                data-handle="remove-list-item-button"
                style={disabled ? style.disabled : style.enabled}
                onClick={onClick}
                disabled={disabled}
                onMouseDown={showTooltip}
                onMouseUp={hideTooltip}
                onMouseEnter={showTooltip}
                onMouseLeave={hideTooltip}
            >
                { !disabled ?
                (<RemoveSymbol />) :
                (
                    <RemoveSymbol
                        symbolColor={defaultStyles.color6}
                        borderColor={defaultStyles.color6}
                        bgColor={defaultStyles.color3}
                    />
                )}
            </button>
            <ToolTip
                direction="right"
                toolTipText="remove device"
                ref={(tt) => { toolTipRef.current = tt; }}
            />
        </div>
    );
}

RemoveDevicesButton.propTypes = {
    disabled: PropTypes.bool,
    onRemoveDevices: PropTypes.func,
};

export default RemoveDevicesButton;
