import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import {
    deleteNodesThunk,
} from 'modules/nodes/thunks';
import {
    selectedDeviceRowsSelector,
    selectDeviceManagerIsLoading,
} from 'modules/devices/selectors';
import RemoveDevicesButton from './RemoveDevicesButton';
import { deviceListPath } from 'modules/devices/paths';

class RemoveDevicesContainer extends React.Component {
    constructor(props) {
        super(props);
        this.deleteSelected = this.deleteSelected.bind(this);
    }
    deleteSelected() {
        const { devices, onDelete } = this.props;
        const paths = devices.map(device => device.rowKey);
        return onDelete(paths, deviceListPath);
    }

    render() {
        const { devices, isLoading } = this.props;
        const disabled = isLoading || devices.length === 0;

        return (
            <RemoveDevicesButton
                disabled={disabled}
                onRemoveDevices={this.deleteSelected}
            />
        );
    }
}

RemoveDevicesContainer.propTypes = {
    devices: PropTypes.arrayOf(Object),
    onDelete: PropTypes.func,
    isLoading: PropTypes.bool,
};

const mapStateToProps = state => ({
    devices: selectedDeviceRowsSelector(state),
    isLoading: selectDeviceManagerIsLoading(state),
});

const mapDispatchToProps = dispatch => ({
    onDelete(paths, loadingPath) {
        return dispatch(deleteNodesThunk({ paths, loadingPath }));
    },
});

export default connect(
    mapStateToProps,
    mapDispatchToProps,
)(RemoveDevicesContainer);

export {
    RemoveDevicesContainer as _RemoveDevicesContainer,
};
